import { attend } from '../resource'

export const toAttend = (attendUserObj) => {
  let option = {
    name: attendUserObj.name,
    card: attendUserObj.card,
    tel: attendUserObj.tel,
    competitonType: attendUserObj.competitonType
  }
  return attend.save(option)
}



