  computed: {},
  ready: function () {},
  attached: function () {},
  methods: {
    attendHandler: function () {
      // 
      let attendObj
      attendObj = {
        name: validator.trim(this.attend.name),
        card: validator.trim(this.attend.card),
        tel: validator.trim(this.attend.tel),
